<template>
  <div class="jumbotron" id="Home">
    <div class="Home-container">
      <div class="Home-subtitle">Demo Landing Page</div>
      <div class="Home-title">Predicting Judicial Decision on Post-Divorce Child Custody through Natural Language Processing</div>
    </div>
    <UserGuide class="Home-intro" :guide="guide" :inHome="false" />
    <router-link to="/nlp-judgement-frontend/predict" class="btn-lg btn-default btn-navigate">Start Predict →</router-link>
  </div>
</template>

<script>
import UserGuide from '../Sub/userGuide';

export default {
  name: 'HomePage',
  components: {
    UserGuide,
  },
  data() {
    return {
      mainIntro:
        '本網頁是國立清華大學執行科技部AI人社領域計畫的成果展示與測試網頁。本計畫是使用司法院所公開釋出的判決書作為訓練資料，運用人工智慧中的自然語言處理技術建立人工智慧模型，針對若干司法案件預測可能的判決結果，提供案件的當事人參考。',
      guide:
         '<ul style="list-style-type: decimal;"><li> This judgment prediction is for the cases of post-divorce child custody, under the culture and law tradition of Taiwan society.</li><li>It is assumed that both parents are willing to raise the child. Please follow the steps below to input relevant information to make judgment prediction. </li><li> Any prediction result is insufficient due to the limitations of model training and does not have any legal effect.</li><li> This demo landing page and AI model is constructed by Y.-N. Juan et al. for the paper submitted for EACL 2023. </li><ul>'
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss" scoped>
@import "vue-select/src/scss/vue-select.scss";
@import "~bootstrap/scss/_functions";
@import "~bootstrap/scss/_variables";
@import "~bootstrap/scss/mixins/_breakpoints";

.jumbotron {
  padding: 1.5rem 1rem;
  margin-bottom: 0px;
  padding-bottom: 50px;
  border-radius: 0px;
  background-color: #fff;
  overflow-y: auto;
}

#Home {
  background-color: #1F191B;
  height: 100%;
  position: relative;
  text-align: center;
  color: white;
}
.Home-container {
  padding: 20px;
}
.Home-title {
  font-size: 2.4rem;
  font-weight: bold;
  max-width: 1170px;
  margin: auto;
  padding: 10px;
}
.Home-subtitle {
  font-size: 2.1rem;
  font-weight: 300;
  padding-top: 10px;
}
.Home-intro {
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 50px;
}

.btn-navigate {
  color: white;
  background-color: rgba(243, 189, 92, 0.29);
  padding: 10px 20px;
  border-radius: 4px;
  border: 2px solid #F3BB5C;
  text-decoration: none;
  font-size: 1.2rem;
  transition: background-color 0.2s ease-in-out;
  &:hover {
    background-color: rgba(243, 189, 92, 1);
  }
}

// Unuse code
.container {
  @media (min-width: 1800px) {
    max-width: 1500px;
  }
}

.reasoningText {
  vertical-align: top;
  border: none;
  background-color: transparent;
}

.description {
  letter-spacing: 4px;
  line-height: 35px;
}

.btn {
  color: white;
  background-color: gray;
  padding: 15px;
  letter-spacing: 2px;
  border-radius: 0px;
  border: none;
  font-weight: 800;
  font-size: 15px;
  transition: background-color 0.2s ease-in-out;
  &:hover {
    background: #d8235e;
  }
}
.user-instruction {
  z-index: 100;
  line-height: 35px;
  letter-spacing: 4px;
  text-align: left;
  margin-top: -25px;
}

</style>



