<template>
    <div class="page-container">
        <div class="page-title">
            Predict
        </div>
        <div class="predict-container">
            <div class="shadow-none p-2 mb-2 rounded quote">You can choose to do either Step I or Step II, or both.</div>
            <div class="row">
                <div class="col">
                    <div class="ins-1 p-1">Step I: Input or Modify sentences to describe your case.</div>
                    <div class="ins-2 p-1">( Please use “The Party” to replace “I” or “He/Her” etc. To reduce a bias prediction, it is encouraged to have same number of statements for both Father and Mother. )</div>
                </div>
                <div class="col">
                    <div class="ins-1 p-1">Step II : Select the feature which are close to your case.</div>
                </div>
            </div>
            <div v-loading="isLoading">
              <div v-for="section in resultType" :key='section.key'>
                <div class="ins-1" >{{section.title}}</div>
                  <div class="row">
                      <div class="col">
                        <div class="row">
                          <div class="col-10">
                            <textarea class="form-control border-secondary" rows="5" v-model="result[section.key].sentence"></textarea>
                          </div>
                          <div class="col-2 p-0">
                            <el-dialog title="Sample Statement" :visible.sync="section.dialog">
                              <div v-for="statementGroup in sentencesExample[section.type]" :key="statementGroup.type" style="padding-bottom:20px;">
                                <div style="padding-bottom: 5px;">{{statementGroup.type}}</div>
                                <div v-for="statement in statementGroup.item" :key="statement">
                                  <div class='statement-card' :class="checkStatement(section.key, statement)?'isActive':''" @click="addStatement(section.key, statement)">
                                    {{statement}}
                                  </div>
                                </div>
                              </div>
                            </el-dialog>
                            <div class="example-btn" @click="section.dialog=true">
                              <table style="height: 100%;">
                                <td class="align-middle text-center">+ Sample Statement</td>
                              </table>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="col">
                          <multiselect v-model="result[section.key].feature" :options="features" :multiple="true" :close-on-select="false" :clear-on-select="false" placeholder="Select some features" label="label" track-by="value">
                          </multiselect>
                      </div>
                  </div>
              </div>
            </div>
            <div class="row pt-5 justify-content-center">
              <div class="col-4">
                <div class="predict-btn" style="background-color:#5A4D30; float:right;" @click="clearAllStatement()">Clear all statements</div>
              </div>
              <div class="col-4">
                <div class="predict-btn" style="background-color:#F3BB5C" @click="startPredict()">Apply for prediction</div>
              </div>
              <PredictResult
                v-if="isStartPredict"
                id="predictResult"
                :predict_result="predict_result"
                :elapsedTime="elapsedTime"
                :isLoading="isLoading"
                :errorPrompt="errorPrompt"
                :errorCode="errorCode"
              />
            </div>
        </div>
    </div>
</template>

<script>
import Multiselect from 'vue-multiselect';
import PredictResult from '../Sub/predictResult';

export default {
  name: 'UserPredict',
  components: {
    Multiselect,
    PredictResult,
  },
  data() {
    return {
      predict_result: [],
      elapsedTime: 0,
      isLoading: false,
      isStartPredict: false,
      errorPrompt: false,
      errorCode: new Error(),
      resultType: [
        { title: 'The statements favorable to Father', type: 'A', key: 'FA', dialog: false },
        { title: 'The statements unfavorable to Father', type: 'D', key: 'FD', dialog: false },
        { title: 'The statements favorable to Mother', type: 'A', key: 'MA', dialog: false },
        { title: 'The statements unfavorable to Mother', type: 'D', key: 'MD', dialog: false },
      ],
      features: [
        { label: '親子感情 Parent-Child Affection', value: '1' },
        { label: '意願能力 Parent\'s WIllingness and Capability', value: '2' },
        { label: '父母經濟 Parent\'s Financial Status', value: '3' },
        { label: '支持系統 Supporting System', value: '4' },
        { label: '父母生活 Parent\'s Living Condition', value: '5' },
        { label: '主要照顧 Primary Caregiver', value: '6' },
        { label: '子女年齡 Child\'s Age', value: '7' },
        { label: '人格發展 Child\'s Development', value: '8' },
        { label: '父母健康 Parent\'s Health Satus', value: '9' },
        { label: '父母職業 Parent\'s Occupation', value: '10' },
        { label: '子女意願 Child\'s Willingness', value: '11' },
        { label: '友善父母 Parent\'s Friendliness to the Other', value: '12' },
        { label: '父母品行 Parent\'s Character', value: '13' },
      ],
      sentencesExample: {
        A: [
          {
            type: '經濟工作情況',
            item: ['當事人有正當工作，經濟能力尚足以支付本身及孩子生活所需，可提供孩子穩定及安全的生活。'],
          },
          {
            type: '孩童個人意願',
            item: ['當孩子對於與家庭成員間互動，何者能給予其安全、滿足之心理環境及生活照顧，已有判斷能力。有表達與當事人共同生活之意願。'],
          },
          {
            type: '平日相處關係',
            item: ['孩子對當事人有一定程度的依賴感及安全感存在。例如孩子會直接對當事人撒嬌求助，並且得到安撫。'],
          },
          {
            type: '主要照顧者',
            item: ['當事人目前為孩子之主要照顧者，並能提供穩定生活及正向發展，並無受不當照顧情形，與當事人互動情形亦良好。'],
          },
          {
            type: '居住環境評估',
            item: ['當事人住處的社區環境足以提供食衣住行育樂的活動安排，而內部居家環境的設備完善，環境寬敞且乾淨整潔，合適孩子居住。'],
          },
          {
            type: '親友支持系統',
            item: ['當事人的家人親戚亦住在附近或同住，可以協助支援孩子的照顧。孩子與其互動亦自然和諧。'],
          },
          {
            type: '過往陪伴經驗',
            item: ['當事人能夠具體陳述對孩子之教育理念及教養態度，且皆會於休假及空閒時，陪伴子女外出遊玩，培養親子互動。'],
          },
          {
            type: '友善父母觀念',
            item: ['當事人願意讓對方探視孩子，繼續維繫親子關係。'],
          },
          {
            type: '其他相關描述',
            item: ['當事人爭取撫養孩子親權之意願積極，動機是希望陪伴孩子在較好的環境中成長。', '孩子現仍處於嬰幼階段，亟需母親的關愛照顧，而當事人長期與其共同生活，對其人格特質、身心發展狀況及生活需求甚為瞭解。'],
          },
        ],
        D: [
          {
            type: '經濟工作情況',
            item: ['當事人目前未有工作收入或收入不穩定，生活支出仰賴家人協助，不確定能否支持養育孩子的經濟需求。'],
          },
          {
            type: '孩童個人意願',
            item: ['孩子年紀已經夠大，已能了解家庭成員互動的關係，意願上比較不希望與當事人同住。'],
          },
          {
            type: '平日相處關係',
            item: ['孩童對當事人於雙方婚姻存續期間之諸多作為感到嫌惡，並因當事人離異時造成心靈傷害，而與之關係疏離、緊張。'],
          },
          {
            type: '主要照顧者',
            item: ['當事人目前並非與孩子同住，或者並非孩子生活的主要照顧者，對其個性、生活所需上了解可能不足。'],
          },
          {
            type: '居住環境評估',
            item: ['當事人的居住地點附近環境複雜，且屋內並無孩子獨立的失活空間，居家環境不太適合孩子成長。'],
          },
          {
            type: '親友支持系統',
            item: ['當事人的其他親戚朋友並未同住或少有往來，與孩子的關係陌生，無法提供足夠的支持系統。'],
          },
          {
            type: '過往陪伴經驗',
            item: ['當事人過往在家庭生活中因為工作或個性，比較少陪伴孩子與之相處，亦不太了解其生活狀況。孩子與之單獨相處會有焦慮。'],
          },
          {
            type: '友善父母觀念',
            item: ['當事人不願意或阻撓對方探視孩子或任何接觸。'],
          },
          {
            type: '其他相關描述',
            item: ['當事人若單獨撫養孩子，因為工作關係，可能沒有時間管教，會繼續放任其偏差之行為，不利於孩子的成長與發展。', '當事人工作的時間不穩定，長需要夜間輪班工作。孩子現尚幼小，故配合當事人活動尚可，但日後恐會影響到就學及生活作息。'],
          },
        ],
      },
      result: {
        FA: { sentence: '', feature: [] },
        FD: { sentence: '', feature: [] },
        MA: { sentence: '', feature: [] },
        MD: { sentence: '', feature: [] },
      },
    };
  },
  methods: {
    addStatement(resultKey, statement) {
      if (this.checkStatement(resultKey, statement)) {
        this.result[resultKey].sentence = this.result[resultKey].sentence.replace(statement, '');
      } else {
        this.result[resultKey].sentence += statement;
      }
    },
    checkStatement(resultKey, statement) {
      if (this.result[resultKey].sentence.indexOf(statement) >= 0) return true;
      return false;
    },
    clearAllStatement() {
      this.result = {
        FA: { sentence: '', feature: [] },
        FD: { sentence: '', feature: [] },
        MA: { sentence: '', feature: [] },
        MD: { sentence: '', feature: [] },
      };
    },
    startPredict() {
      console.log(this.result);
      this.isStartPredict = true;
      this.isLoading = true;
    },
  },
};
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css"></style>
<style lang="scss" scoped>
.page-container {
    width: calc( 90% - 40px );
    max-width: 1680px;
    margin: auto;
}
.page-title {
    font-size: 1.5rem;
    font-weight: bold;
    width: fit-content;
    border-bottom: 2px solid #F3BB5C;
    color: white;
    margin: 30px 0 20px 0;
    padding: 0 5px;
}
.predict-container {
    background-color: #fff;
    border: 2px solid #F3BB5C;
    border-radius: 8px;
    padding: 35px 40px;
    margin-bottom: 50px;
}
.ins-1 {
    font-size: 1.2rem;
    font-weight: bold;
    padding-top: 15px;
}
.quote {
    background-color: rgba(90,77,88,0.12);
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold;
}
.example-btn {
    border-radius: 4px;
    background-color: #5A4D30;
    width: 100%;
    height: 100%;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: 0.4s;
    &:hover {
        opacity: 0.8;
    }
}
.predict-btn {
    border-radius: 4px;
    color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
    width: fit-content;
    text-align: center;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.4s;
    &:hover {
        opacity: 0.8;
    }
}
.statement-card {
  padding: 5px;
  margin-bottom: 5px;
  border: 1px solid #5A4D30;
  border-radius: 2px;
  cursor: pointer;
  transition: 0.4s;
  &:hover {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  }
}
.isActive {
  background: #5A4D30;
  color: #fff;
}
</style>



