<template>
  <div id="container-guide" class="container" style="z-index: 100">
    <div id="guide">
      <h4 class="user-instruction-title" :class="{'home-user-instruction-title': isHome}">Instructions:</h4>
      <h6 class="user-instruction" :class="{'home-user-instruction': isHome}">
        <div v-html="guide"></div>
      </h6>
    </div>
  </div>
</template>

<script>
export default {
  name: 'userGuide',
  props: {
    mainIntro: '',
    guide: '',
    isHome: false,
  },
};
</script>

<style lang="scss" scoped>

@font-face {
  font-family: 'cwTeXKai';
  font-style: normal;
  font-weight: 500;
  src: url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.eot);
  src: url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.eot?#iefix) format('embedded-opentype'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.woff2) format('woff2'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.woff) format('woff'),
       url(//fonts.gstatic.com/ea/cwtexkai/v3/cwTeXKai-zhonly.ttf) format('truetype');
}


#mainIntro {
  position: relative;
  font-size: 20pt;
  text-align: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  padding: 20px;
  margin-top: 30px;
}

#guide {
  border-radius: 12px;
  padding: 20px;
  background-color: rgba(243, 187, 92, 0.08);
  border: 2px solid #CFBF84;
}

#container-guide {
  width: 85%;
  @media (min-width: 1400px) {
    max-width: 1280px;
  }
  @media (min-width: 1000px) and (max-width: 1400px) {
    max-width: 11700px;
  }
  @media (max-width: 1000px) {
    max-width: 760px;
  }
}
.description {
  letter-spacing: 4px;
}
.user-instruction {
  font-size: 1.2rem;
  z-index: 100;
  line-height: 30px;
  // letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
}
.home-description {
  letter-spacing: 4px;
  line-height: 35px;
  padding: 15px;
  font-size: 1.1rem;
}
.home-user-instruction {
  z-index: 100;
  line-height: 35px;
  letter-spacing: 4px;
  // margin-left: 50%;
  // transform: translateX(-50%);
  text-align: left;
  margin-top: 20px;
  font-size: 1.05rem;
}
.home-user-instruction-title {
  margin-top: 20px;
}
.user-instruction-title {
  text-align: left;
}
</style>



