<template>
  <section id="predict-result">
    <div id="btn-det">
      <div class="w-100 justify-content-center"></div>
    </div>
    <div class="d-none d-md-block" style="z-index: 100">
      <div class="d-inline-flex w-100 justify-content-center mt-4" style="z-index: 100">
        <h5>Probability to Father: {{predict_result['Applicant']}}%</h5>
        <h5 class="ml-4">Probability to Mother: {{predict_result['Respondent']}}%</h5>
        <h5 class="ml-4">Probability to Both Parties: {{predict_result['Both']}}%</h5>
      </div>
      <div v-if="isLoading">Processing......have passed {{elapsedTime}} sec.</div>
      <div v-if="errorPrompt">{{errorCode}}</div>
      <h6 class="mt-2">(Users are encouraged to try many times by rephrasing the statements. Please contact us having any questions.)</h6>
    </div>
    <div class="d-md-none" style="z-index: 100">
      <div class="w-100 justify-content-center mt-4" style="z-index: 100">
        <h5>Probability to Father: {{predict_result['Applicant']}}%</h5>
        <h5 class="ml-4 mt-3">Probability to Mother: {{predict_result['Respondent']}}%</h5>
        <h5 class="ml-4 mt-3">Probability to Both Parties {{predict_result['Both']}}%</h5>
      </div>
      <div v-if="isLoading">rocessing......have passed {{elapsedTime}} sec.</div>
      <div v-if="errorPrompt">{{errorCode}}</div>
      <h6 class="mt-2">(Users are encouraged to try many times by rephrasing the statements. Please contact us having any questions.)</h6>
    </div>
  </section>
</template>

<script>
export default {
  name: 'PredictResult',
  data() {
    return {};
  },
  props: {
    predict_result: Array,
    elapsedTime: Number,
    isLoading: Boolean,
    errorPrompt: Boolean,
    errorCode: Error,
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
* {
  text-align: center;
}
.btn {
  color: white;
  background-color: #802a2a;
  padding: 15px;
  letter-spacing: 2px;
  border-radius: 0px;
  border: none;
  font-weight: 800;
  font-size: 15px;
  transition: background-color 0.2s ease-in-out;
  &:hover {
    background: #d8235e;
  }
}
</style>



